import React from 'react'
import {Navigate, Routes, Route, Outlet} from 'react-router-dom'
import {PageLink, PageTitle} from '../../../_metronic/layout/core'
import {PeopleHeader} from './PeopleHeader'
import {Overview} from './components/Overview'
import {Professor} from './components/Professor'
import {Student} from './components/Student'
import {Alumni} from './components/Alumni'


const peopleBreadCrumbs: Array<PageLink> = [
  {
    title: 'People',
    path: '/people/overview',
    isSeparator: false,
    isActive: false,
  },
  {
    title: '',
    path: '',
    isSeparator: true,
    isActive: false,
  },
]

const PeoplePage = () => (
  <Routes>
    <Route
      element={
        <>
          <PeopleHeader />
          <Outlet />
        </>
      }
    >
      <Route
        path='overview'
        element={
          <>
            <PageTitle breadcrumbs={peopleBreadCrumbs}>Overview</PageTitle>
            <Overview />
          </>
        }
      />
      <Route
        path='professor'
        element={
          <>
            <PageTitle breadcrumbs={peopleBreadCrumbs}>Professor</PageTitle>
            <Professor />
          </>
        }
      />
      <Route
        path='student'
        element={
          <>
            <PageTitle breadcrumbs={peopleBreadCrumbs}>Student</PageTitle>
            <Student />
          </>
        }
      />
      <Route
        path='alumni'
        element={
          <>
            <PageTitle breadcrumbs={peopleBreadCrumbs}>Alumni</PageTitle>
            <Alumni />
          </>
        }
      />
    </Route>
  </Routes>
)

export default PeoplePage